<!-- eslint-disable -->
<template>
  <!-- <div> -->
  <footer>
    <div class="container">
      <div class="line"></div>
      <div class="wrapper">
        <div class="sections">
          <div class="section">
            <div class="title">Contactos</div>
            <div class="text">
              <span>{{tlf1}}</span>
              <br />
              <a class="link" :href="'mailto:' + email1">{{email1}}</a>
              <br />
              <p v-html="address"></p>
            </div>
          </div>
          <div class="section">
            <div class="title">Serviços</div>
            <div class="links">
              <div class="home">
                <a class="link" v-for="e in especialidades" :key="e.id" @click="toHash(e.code)">{{e.title}}</a>
              </div>
              <div class="no-home">
                <router-link
                  v-for="e in especialidades"
                  :key="e.id"
                  class="link"
                  :to="e.url"
                >{{e.title}}</router-link>
              </div>
            </div>
          </div>
          <div class="section">
            <div class="title">Links Úteis</div>
            <div class="links">
              <router-link class="link" to="/sobre-nos">Sobre Nós</router-link>
              <div class="sb">
                <a class="link" @click="toHash('equipa')">A Nossa Equipa</a>
              </div>
              <div class="no-sb">
                <router-link class="link" to="/sobre-nos#equipa">A Nossa Equipa</router-link>
              </div>
              <router-link class="link" to="/politica-privacidade">Política de Privacidade</router-link>
              <router-link class="link" to="/perguntas-frequentes">Perguntas Frequentes</router-link>
              <a class="link" @click="openContactForm()">Marcação de Consulta</a>
            </div>
          </div>
        </div>
        <div class="social-icons">
          <a :href="facebook" target="_blank">
            <div class="icon">
              <img :src="fb" alt />
            </div>
          </a>
          <a :href="instagram" target="_blank">
            <div class="icon">
              <img :src="ig" alt />
            </div>
          </a>
          <!-- <div class="icon">
            <img :src="world" alt />
          </div>-->
        </div>
      </div>
      <!-- <a class="to-top" href="#main-section">
        <div id="btt-btn">
          <div id="arrow">
            <i class="arrow up"></i>
            Topo
          </div>
        </div>
      </a>-->
    </div>
  </footer>
  <!-- </div> -->
</template>

<script>
/* eslint-disable */
import IG_logo from "../../assets/ig.svg";
import FB_logo from "../../assets/fb.svg";
import axios from "axios";

export default {
  data() {
    return {
      ig: IG_logo,
      fb: FB_logo,
      especialidades: [
        {
          id: 1,
          title: "",
          url: "",
          code: "",
        },
      ],
      tlf1: "",
      tlf2: "",
      email1: "",
      email2: "",
      address: "",
      facebook: "",
      instagram: "",
    };
  },
  methods: {
    openContactForm() {
      var contact_form = document.querySelector("#contact-form");
      var menu = document.querySelector("#menu");

      contact_form.style.transition = "1s";
      contact_form.style.transform = "translate(-50%, -50%)";
      contact_form.style.pointerEvents = "all";
      $("#contact-form .page").css("pointer-events", "all");
      var html_elem = document.documentElement;
      html_elem.style.overflow = "hidden";

      $(".menu-wrapper-mobile .menu-items").removeClass("open");
      $(".menu-wrapper-mobile .btn-menu").removeClass("open");
      $(".menu-wrapper-mobile .close-menu").removeClass("open");

      $("#contact-form .p0 .text").animate({ left: 0, opacity: 1 }, 500);
    },
    toHash(hash){
      var top = $("#" + hash)[0].offsetTop;
      $('html, body').animate({scrollTop: top - 100}, 1000)
    }
  },

  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get(
        "https://beta.anacarolinapereira.pt/db-api/getEspecialidadesHomepage.php"
      )
      .then((resp) => {
        const data = resp.data;
        var especialidades = [];

        map.call(data, function (d) {
          var split = d.link.split("#");
          especialidades.push({
            id: d.id,
            title: d.title,
            url: d.link,
            code: split[1],
          });
        });

        vm.$data.especialidades = especialidades;
      });

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getContactosHomepage.php")
      .then((resp) => {
        const data = resp.data;

        vm.tlf1 = data[0].tlf_1;
        if (data[0].tlf_2 == "") $("#tlf2").css("display", "none");
        else {
          $("#tlf2").css("display", "inline-block");
          vm.tlf2 = " ou " + data[0].tlf_2;
        }

        vm.email1 = data[0].email_1;
        if (data[0].email_2 == "") $("#email2").css("display", "none");
        else {
          $("#email2").css("display", "inline-block");
          vm.email2 = " ou " + data[0].email_2;
        }

        vm.address = data[0].address;

        vm.facebook = data[0].facebook;
        vm.instagram = data[0].instagram;
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(130);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

footer {
  height: pxToEm(470);
  background-color: #95989a;
  padding: $pd-desktop;
  color: $white;
  display: flex;
  align-items: center;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (max-width: 767px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  .container {
    height: 75%;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: pxToEm(1500);

    @media only screen and (max-width: 767px) {
      margin: 0 pxToEm(20);
      margin-bottom: pxToEm(20);
    }

    .line {
      width: 100%;
      height: pxToEm(1);
      background-color: $white;
      margin-bottom: pxToEm(40);

      @media only screen and (max-width: 767px) {
        margin: pxToEm(40) 0 pxToEm(20) 0;
      }

      @media only screen and (max-width: 599px) {
        display: none;
      }
    }

    .wrapper {
      height: 100%;
      display: flex;
      justify-content: space-between;

      @media only screen and (max-width: 599px) {
        flex-direction: column-reverse;
        margin-top: pxToEm(40);
      }

      .sections {
        display: flex;
        max-width: pxToEm(1000);
        width: 100%;
        justify-content: space-between;
        margin-left: pxToEm(100);

        @media only screen and (max-width: 767px) {
          margin-left: 0;
        }

        @media only screen and (max-width: 599px) {
          flex-wrap: wrap;
          margin-bottom: pxToEm(20);
        }

        .section {
          @media only screen and (max-width: 599px) {
            padding: pxToEm(20) 0;
            border-bottom: 1px solid #fff;
            width: 100%;

            &:last-child {
              border: none;
              padding-bottom: 0;
            }
          }

          .title {
            font-family: "OS-Regular";
            font-size: pxToEm(21);
            text-transform: uppercase;
            margin-bottom: pxToEm(20);
            font-weight: bold;
          }

          .text {
            font-size: pxToEm(20);
            font-family: "OS-Regular";
            line-height: 1.9em;

            .link {
              font-size: pxToEm(20);
              color: $white;
              text-decoration: none;
              // height: pxToEm(35);
              line-height: 1.9em;
              font-family: "OS-Regular";
              cursor: pointer;
              transition: 0.5s;

              &:hover {
                color: #cbcbcb;
                transition: 0.5s;
              }
            }
          }

          .links {
            display: flex;
            flex-direction: column;

            .link {
              font-size: pxToEm(20);
              color: $white;
              text-decoration: none;
              // height: pxToEm(35);
              line-height: 1.9em;
              font-family: "OS-Regular";
              cursor: pointer;
              transition: 0.5s;

              &:hover {
                color: #cbcbcb;
                transition: 0.5s;
              }
            }

            > div {
              display: flex;
              flex-direction: column;

              .link {
                font-size: pxToEm(20);
                color: $white;
                text-decoration: none;
                // height: pxToEm(35);
                line-height: 1.9em;
                font-family: "OS-Regular";
                cursor: pointer;
                transition: 0.5s;

                &:hover {
                  color: #cbcbcb;
                  transition: 0.5s;
                }
              }
            }
          }
        }
      }

      .social-icons {
        display: flex;
        width: pxToEm(90);
        justify-content: space-between;

        @media only screen and (max-width: 599px) {
          padding-bottom: pxToEm(20);
          border-bottom: 1px solid #fff;
          width: 100%;
          justify-content: flex-start;
        }

        .icon {
          @media only screen and (max-width: 599px) {
            margin-right: pxToEm(20);
          }
        }
      }
    }

    // .to-top {
    //   width: pxToEm(40);
    //   height: pxToEm(40);
    //   display: flex;
    //   align-items: center;
    //   justify-content: center;
    //   align-self: flex-end;
    //   color: #fff;

    //   #btt-btn {
    //     width: pxToEm(40);
    //     height: pxToEm(40);
    //     display: flex;
    //     align-items: center;
    //     justify-content: center;
    //     align-self: flex-end;
    //     cursor: pointer;
    //     transition: 0.5s;

    //     &:hover {
    //       color: #cbcbcb;

    //       #arrow {
    //         .arrow {
    //           border: solid #cbcbcb;
    //           border-width: 0 pxToEm(3) pxToEm(3) 0;
    //           transition: 0.5s;
    //         }
    //       }
    //     }

    //     #arrow {
    //       text-align: center;

    //       .arrow {
    //         border: solid #fff;
    //         border-width: 0 pxToEm(3) pxToEm(3) 0;
    //         display: inline-block;
    //         padding: pxToEm(5);
    //         transition: 0.5s;

    //         &.up {
    //           transform: rotate(-135deg);
    //           -webkit-transform: rotate(-135deg);
    //         }
    //       }
    //     }
    //   }
    // }
  }
}
</style>



