<!--eslint-disable-->
<template>
  <div id="contact-form">
    <div class="content-wrapper">
      <div class="close-btn" @click="closeForm()">
        <div class="icon">
          <div class="line"></div>
          <div class="line"></div>
        </div>
      </div>
      <div class="pages">
        <div class="page p3" tabindex="3">
          <div class="indicators">
            <div class="line"></div>
            <div class="indicator p0">
              <div class="point"></div>
            </div>
            <div
              class="indicator p1"
              :style="{'background-image': 'url('+check+')'}"
              style="background-size: cover;"
            ></div>
            <div class="indicator p2">
              <div class="point"></div>
            </div>
          </div>
          <div class="text">
            <div class="title-success">A seu pedido de consulta foi enviado com sucesso!</div>
            <div class="copy">Muito obrigado! Receberá um contacto breve da nossa parte!</div>

            <div class="btn-inicio">
              <div class="btn">
                <div class="txt">Início</div>
              </div>
            </div>
          </div>
        </div>

        <div class="page p2" tabindex="2">
          <div class="indicators">
            <div class="line"></div>
            <div class="indicator p0">
              <div class="point"></div>
            </div>
            <div class="indicator p1">
              <div class="point"></div>
            </div>
            <div class="indicator p2 active-pg">
              <div class="point"></div>
            </div>
          </div>
          <div class="text">
            <div class="question">Qual a especialidade pretendida?</div>
            <div class="inputs">
              <div class="input-container">
                <div class="custom-select">
                  <select id="subject" name="subject">
                    <option
                      class="default"
                      value="none"
                      disabled
                      selected
                    >Seleccione a especialidade</option>
                    <option value="consulta-adulto">Consulta | Adulto</option>
                    <option value="consulta-jovem">Consulta | Jovem</option>
                    <option value="consulta-criança">Consulta | Criança</option>
                    <option value="outro">Outro</option>
                  </select>
                </div>
                <div
                  class="balloon balloon-subject-error"
                  :style="{'background-image': 'url(' + balloon + ')'}"
                  style="top: -4px;right: -270px;width: 250px;height: 45px;"
                >
                  <div>Por favor seleccione a especialidade.</div>
                </div>
              </div>
              <div class="accept-tc">
                <div class="input-container">
                  <label for="accept-tc">
                    <input type="checkbox" id="accept-tc" />
                    Li e aceito a
                    <router-link to="/politica-privacidade">Política de Privacidade</router-link>
                  </label>
                  <div
                    class="balloon balloon-tc-error"
                    :style="{'background-image': 'url(' + balloon + ')'}"
                    style="top: -15px;right: -270px;width: 250px;height: 60px;"
                  >
                    <div>
                      Por favor confirme que aceita
                      <br />a Política de Privacidade.
                    </div>
                  </div>
                </div>
              </div>
              <button class="submit-btn">
                <div>Submeter</div>
              </button>
            </div>
          </div>
        </div>

        <div class="page p1" tabindex="1">
          <div class="indicators">
            <div class="line"></div>
            <div class="indicator p0">
              <div class="point"></div>
            </div>
            <div class="indicator p1 active-pg">
              <div class="point"></div>
            </div>
            <div class="indicator p2">
              <div class="point"></div>
            </div>
          </div>
          <div class="text">
            <div class="question">Para que o/a possamos contactar, selecione uma opção.</div>
            <div class="radio">
              <div class="option">
                <label class="opt" for="contact-phone">
                  <input
                    id="contact-phone"
                    type="radio"
                    value="contact-phone"
                    name="contact-option"
                    checked="true"
                  />
                  <span class="radio-btn"></span>
                  Telefone
                </label>
              </div>

              <div class="option">
                <label class="opt" for="contact-email">
                  <input
                    id="contact-email"
                    type="radio"
                    value="contact-email"
                    name="contact-option"
                  />
                  <span class="radio-btn"></span>
                  E-mail
                </label>
              </div>

              <div class="option">
                <label class="opt" for="contact-both">
                  <input id="contact-both" type="radio" value="contact-both" name="contact-option" />
                  <span class="radio-btn"></span>
                  Ambos
                </label>
              </div>
            </div>
            <div class="inputs">
              <div class="input-container">
                <input
                  type="number"
                  name="contact-phone"
                  id="contact-phone-active"
                  placeholder="ESCREVA AQUI O SEU Telefone"
                />
                <div
                  class="balloon balloon-phone-error"
                  :style="{'background-image': 'url(' + balloon + ')'}"
                >
                  <div>Por favor escreva o seu telefone.</div>
                </div>
              </div>
              <div class="input-container" style="display: flex; position: relative;">
                <input
                  type="email"
                  name="contact-email"
                  id="contact-email-active"
                  placeholder="ESCREVA AQUI O SEU E-MAIL"
                  style="display: none;"
                />
                <div
                  class="balloon balloon-email"
                  :style="{'background-image': 'url(' + balloon + ')'}"
                  style="display: none;"
                >
                  <div>example@example.com</div>
                </div>
                <div
                  class="balloon balloon-email-error"
                  :style="{'background-image': 'url(' + balloon + ')'}"
                >
                  <div>Por favor escreva o seu e-mail.</div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="page p0 active-pg" tabindex="0">
          <div class="indicators">
            <div class="line"></div>
            <div class="indicator p0 active-pg">
              <div class="point"></div>
            </div>
            <div class="indicator">
              <div class="point"></div>
            </div>
            <div class="indicator">
              <div class="point"></div>
            </div>
          </div>
          <div class="text">
            <div class="question">Agradecemos o seu contacto! Pode-nos dizer o seu nome?</div>
            <div class="inputs">
              <div class="input-container">
                <input
                  id="contact-name"
                  type="text"
                  name="contact-name"
                  placeholder="ESCREVA AQUI O SEU NOME"
                />
                <div
                  class="balloon balloon-name-error"
                  :style="{'background-image': 'url(' + balloon + ')'}"
                >
                  <div>Por favor escreva o seu nome.</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import balloon from "../../assets/blue-balloon.png";
import check from "../../assets/check.png";
import FadeIn from "../../animations/fade";

export default {
  data() {
    return {
      balloon: balloon,
      check: check,
      FadeIn,
    };
  },
  methods: {
    closeAllSelect(elmnt) {
      /*a function that will close all select boxes in the document,
      except the current select box:*/
      var x,
        y,
        i,
        xl,
        yl,
        arrNo = [];
      x = document.getElementsByClassName("select-items");
      y = document.getElementsByClassName("select-selected");
      xl = x.length;
      yl = y.length;
      for (i = 0; i < yl; i++) {
        if (elmnt == y[i]) {
          arrNo.push(i);
        } else {
          y[i].classList.remove("select-arrow-active");
        }
      }
      for (i = 0; i < xl; i++) {
        if (arrNo.indexOf(i)) {
          x[i].classList.add("select-hide");
        }
      }
    },
    addSelectElements(vm) {
      var x, i, j, l, ll, selElmnt, a, aa, b, c, d;
      /*look for any elements with the class "custom-select":*/
      x = document.getElementsByClassName("custom-select");
      l = x.length;
      for (i = 0; i < l; i++) {
        selElmnt = x[i].getElementsByTagName("select")[0];
        ll = selElmnt.length;
        /*for each element, create a new DIV that will act as the selected item:*/
        a = document.createElement("DIV");
        a.setAttribute("class", "select-selected");
        // a.setAttribute("data-v-6aabded5", "");
        // a.innerHTML = selElmnt.options[selElmnt.selectedIndex].innerHTML;
        x[i].appendChild(a);
        aa = document.createElement("DIV");
        // aa.setAttribute("data-v-6aabded5", "");
        aa.innerHTML = selElmnt.options[selElmnt.selectedIndex].innerHTML;
        a.appendChild(aa);
        /*for each element, create a new DIV that will contain the option list:*/
        b = document.createElement("DIV");
        b.setAttribute("class", "select-items select-hide");
        // b.setAttribute("data-v-6aabded5", "");
        for (j = 1; j < ll; j++) {
          /*for each option in the original select element,
        create a new DIV that will act as an option item:*/
          c = document.createElement("DIV");
          // c.setAttribute("data-v-6aabded5", "");
          c.innerHTML = selElmnt.options[j].innerHTML;
          c.addEventListener("click", function (e) {
            /*when an item is clicked, update the original select box,
            and the selected item:*/
            var y, i, k, s, h, sl, yl;
            s = this.parentNode.parentNode.getElementsByTagName("select")[0];
            sl = s.length;
            h = this.parentNode.previousSibling;
            for (i = 0; i < sl; i++) {
              if (s.options[i].innerHTML == this.innerHTML) {
                d = document.createElement("DIV");
                // d.setAttribute("data-v-6aabded5", "");
                d.innerHTML = s.options[i].innerHTML;
                s.selectedIndex = i;
                h.innerHTML = "";
                h.appendChild(d);
                y = this.parentNode.getElementsByClassName("same-as-selected");
                yl = y.length;
                for (k = 0; k < yl; k++) {
                  y[k].removeAttribute("class");
                }
                this.setAttribute("class", "same-as-selected");
                // this.setAttribute("data-v-6aabded5", "");
                break;
              }
            }
            h.click();
          });
          b.appendChild(c);
        }
        x[i].appendChild(b);

        a.addEventListener("click", function (e) {
          /*when the select box is clicked, close any other select boxes,
        and open/close the current select box:*/
          e.stopPropagation();
          vm.closeAllSelect(this);
          this.nextSibling.classList.toggle("select-hide");
          this.classList.toggle("select-arrow-active");
        });
      }
    },
    getFormData() {
      var vm = this;
      $(document).ready(function () {
        $("#contact-form .page").css("pointer-events", "none");
        var form_data = new FormData();

        var page_0 = document.querySelector("#contact-form .page.p0");
        var page_1 = document.querySelector("#contact-form .page.p1");
        var page_2 = document.querySelector("#contact-form .page.p2");
        var name = document.querySelector("#contact-form #contact-name");

        $('#contact-form div[tabindex="0"]').keyup(function (e) {
          e.preventDefault();

          if (e.key === "Enter") {
            if (name.value == "") {
              $("#contact-form .balloon-name-error").css("display", "flex");
            } else {
              $("#contact-form .balloon-name-error").css("display", "none");
              form_data.append("name_form", name.value);
              $("#contact-form .p0").removeClass("active-pg");
              $("#contact-form .p1").addClass("active-pg");

              $("#contact-form .p0").css("pointer-events", "none");
              $("#contact-form .p1").css("pointer-events", "all");
              $("#contact-form .p2").css("pointer-events", "none");
              $("#contact-form .p3").css("pointer-events", "none");

              $("#contact-form .p1 .text").animate(
                { left: 0, opacity: 1 },
                500
              );
            }
          }
        });

        var radios = document.querySelectorAll(
          '#contact-form input[name="contact-option"]'
        );
        var value;
        var map = Array.prototype.map;
        var phone_input;
        var email_input;
        var balloon_email;

        map.call(radios, function (r) {
          r.addEventListener("click", function () {
            if (r.type === "radio") {
              if (r.checked) {
                value = r.value;
                form_data.append("contact_form", value);
                phone_input = document.querySelector(
                  "#contact-form #contact-phone-active"
                );
                email_input = document.querySelector(
                  "#contact-form #contact-email-active"
                );
                balloon_email = document.querySelector(
                  "#contact-form .balloon-email"
                );

                if (value == "contact-phone") {
                  phone_input.style.display = "inline-block";
                  email_input.style.display = "none";
                  balloon_email.style.display = "none";

                  form_data.append("contact_phone", phone_input.value);
                  form_data.append("contact_email", email_input.value);
                } else if (value == "contact-email") {
                  phone_input.style.display = "none";
                  email_input.style.display = "inline-block";
                  balloon_email.style.display = "flex";

                  form_data.append("contact_phone", phone_input.value);
                  form_data.append("contact_email", email_input.value);
                } else {
                  phone_input.style.display = "inline-block";
                  email_input.style.display = "inline-block";
                  balloon_email.style.display = "flex";

                  form_data.append("contact_phone", phone_input.value);
                  form_data.append("contact_email", email_input.value);
                }
              }
            }
          });
        });

        $('#contact-form div[tabindex="1"]').keyup(function (e) {
          e.preventDefault();
          phone_input = document.querySelector(
            "#contact-form #contact-phone-active"
          );
          email_input = document.querySelector(
            "#contact-form #contact-email-active"
          );

          if (e.keyCode == 13) {
            if (
              phone_input.value == "" &&
              email_input.style.display == "none"
            ) {
              $("#contact-form .balloon-phone-error").css("display", "flex");
              $("#contact-form .balloon-email-error").css("display", "none");
            }

            if (
              email_input.value == "" &&
              phone_input.style.display == "none"
            ) {
              $("#contact-form .balloon-phone-error").css("display", "none");
              $("#contact-form .balloon-email-error").css("display", "flex");
            }

            if (
              phone_input.value == "" &&
              email_input.value == "" &&
              phone_input.style.display == "inline-block" &&
              email_input.style.display == "inline-block"
            ) {
              $("#contact-form .balloon-phone-error").css("display", "flex");
              $("#contact-form .balloon-email-error").css("display", "flex");
            }

            if (
              phone_input.value == "" &&
              email_input.value != "" &&
              phone_input.style.display == "inline-block" &&
              email_input.style.display == "inline-block"
            ) {
              $("#contact-form .balloon-phone-error").css("display", "flex");
              $("#contact-form .balloon-email-error").css("display", "none");
            }

            if (
              phone_input.value != "" &&
              email_input.value == "" &&
              phone_input.style.display == "inline-block" &&
              email_input.style.display == "inline-block"
            ) {
              $("#contact-form .balloon-phone-error").css("display", "none");
              $("#contact-form .balloon-email-error").css("display", "flex");
            }

            if (
              (phone_input.value != "" && email_input.value != "") ||
              (phone_input.value != "" &&
                email_input.style.display == "none") ||
              (email_input.value != "" && phone_input.style.display == "none")
            ) {
              $("#contact-form .balloon-phone-error").css("display", "none");
              $("#contact-form .balloon-email-error").css("display", "none");
              $("#contact-form .p1").removeClass("active-pg");
              $("#contact-form .p2").addClass("active-pg");

              $("#contact-form .p0").css("pointer-events", "none");
              $("#contact-form .p1").css("pointer-events", "none");
              $("#contact-form .p2").css("pointer-events", "all");
              $("#contact-form .p3").css("pointer-events", "none");

              $("#contact-form .p2 .text").animate(
                { left: 0, opacity: 1 },
                500
              );
            }
          }
        });

        var submit_btn = document.querySelector("#contact-form .submit-btn");
        var assunto = document.querySelector("#contact-form .select-selected");
        var accept_tc = document.querySelector("#contact-form #accept-tc");

        submit_btn.addEventListener("click", function () {
          if (accept_tc.checked != true) {
            $("#contact-form .balloon-tc-error").css("display", "flex");
          } else {
            $("#contact-form .balloon-tc-error").css("display", "none");
          }

          if (assunto.innerText == "SELECCIONE O ASSUNTO") {
            $("#contact-form .balloon-subject-error").css("display", "flex");
          }

          var observer = new MutationObserver(function (mutations) {
            var subject = $("#contact-form .select-selected").text();
            var referrer = document.referrer;
            $("#contact-form .balloon-subject-error").css("display", "none");
          });

          var config = {
            attributes: true,
            childList: true,
            characterData: true,
          };

          observer.observe(assunto, config);

          if (
            assunto.innerText != "SELECCIONE O ASSUNTO" &&
            accept_tc.checked == true
          ) {
            var data = {
              name: name.value,
              phone: phone_input.value,
              email: email_input.value,
              subject: assunto.innerText,
            };

            $.ajax({
              url:
                "https://beta.anacarolinapereira.pt/ajax/marcar-consulta.php",
              type: "POST",
              data: data,
              dataType: "json",
              success: function (response) {
                $("#contact-form .balloon-tc-error").css("display", "none");
                $("#contact-form .balloon-subject-error").css(
                  "display",
                  "none"
                );

                $("#contact-form .p2").removeClass("active-pg");
                $("#contact-form .p3").addClass("active-pg");

                $("#contact-form .p0").css("pointer-events", "none");
                $("#contact-form .p1").css("pointer-events", "none");
                $("#contact-form .p2").css("pointer-events", "none");
                $("#contact-form .p3").css("pointer-events", "all");

                $("#contact-form .p3 .text").animate(
                  { left: 0, opacity: 1 },
                  500
                );
              },
            });

            // $("#contact-form .balloon-tc-error").css("display", "none");
            // $("#contact-form .balloon-subject-error").css("display", "none");

            // $("#contact-form .p2").removeClass("active-pg");
            // $("#contact-form .p3").addClass("active-pg");

            // $("#contact-form .p0").css("pointer-events", "none");
            // $("#contact-form .p1").css("pointer-events", "none");
            // $("#contact-form .p2").css("pointer-events", "none");
            // $("#contact-form .p3").css("pointer-events", "all");

            // $("#contact-form .p3 .text").animate({ left: 0, opacity: 1 }, 500);
          }
        });

        $('#contact-form div[tabindex="2"]').keyup(function (e) {
          e.preventDefault();
          if (e.keyCode == 13) {
            if (accept_tc.checked != true) {
              $("#contact-form .balloon-tc-error").css("display", "flex");
            } else {
              $("#contact-form .balloon-tc-error").css("display", "none");
            }

            if (assunto.innerText == "SELECCIONE O ASSUNTO") {
              $("#contact-form .balloon-subject-error").css("display", "flex");
            }

            var observer = new MutationObserver(function (mutations) {
              var subject = $("#contact-form .select-selected").text();
              var referrer = document.referrer;
              $("#contact-form .balloon-subject-error").css("display", "none");
            });

            var config = {
              attributes: true,
              childList: true,
              characterData: true,
            };

            observer.observe(assunto, config);

            if (
              assunto.innerText != "SELECCIONE O ASSUNTO" &&
              accept_tc.checked == true
            ) {
              var data = {
                name: name.value,
                phone: phone_input.value,
                email: email_input.value,
                subject: assunto.innerText,
              };

              $.ajax({
                url:
                  "https://beta.anacarolinapereira.pt/ajax/marcar-consulta.php",
                type: "POST",
                data: data,
                dataType: "json",
                success: function (response) {
                  $("#contact-form .balloon-tc-error").css("display", "none");
                  $("#contact-form .balloon-subject-error").css(
                    "display",
                    "none"
                  );

                  $("#contact-form .p2").removeClass("active-pg");
                  $("#contact-form .p3").addClass("active-pg");

                  $("#contact-form .p0").css("pointer-events", "none");
                  $("#contact-form .p1").css("pointer-events", "none");
                  $("#contact-form .p2").css("pointer-events", "none");
                  $("#contact-form .p3").css("pointer-events", "all");

                  $("#contact-form .p3 .text").animate(
                    { left: 0, opacity: 1 },
                    500
                  );
                },
              });
            }
          }
        });

        var btn_inicio = document.querySelector("#contact-form .btn-inicio");

        btn_inicio.addEventListener("click", function () {
          $("#contact-form .p3").removeClass("active-pg");
          $("#contact-form .p0").addClass("active-pg");

          $("#contact-form .p0").css("pointer-events", "all");
          $("#contact-form .p1").css("pointer-events", "none");
          $("#contact-form .p2").css("pointer-events", "none");
          $("#contact-form .p3").css("pointer-events", "none");

          $("#contact-form").find("input[type=text]").val("");
          $("#contact-form").find("textarea").val("");
          $("#contact-form").find("input[type=number]").val("");
          $("#contact-form").find("input[type=email]").val("");
          $("#contact-form").find("#contact-phone").prop("checked", true);
          $("#contact-form").find("#accept-tc").prop("checked", false);
          $("#contact-form").find("select").val("");
          document.querySelector("#contact-form .select-selected").innerHTML =
            "<div>SELECCIONE O ASSUNTO</div>";

          var email_input = document.querySelector(
            "#contact-form #contact-email-active"
          );
          email_input.style.display = "none";

          $("#contact-form .page .text").animate(
            { left: "-100px", opacity: 0 },
            0
          );
          $("#contact-form .p0 .text").animate({ left: 0, opacity: 1 }, 500);

          vm.closeForm();

          setTimeout(() => {
            if (vm.$router.history.current.path != "/") vm.$router.push("/");
          }, 600);
        });

        $('#contact-form div[tabindex="3"]').keyup(function (e) {
          e.preventDefault();

          if (e.keyCode == 13) {
            $("#contact-form .p3").removeClass("active-pg");
            $("#contact-form .p0").addClass("active-pg");

            $("#contact-form .p0").css("pointer-events", "all");
            $("#contact-form .p1").css("pointer-events", "none");
            $("#contact-form .p2").css("pointer-events", "none");
            $("#contact-form .p3").css("pointer-events", "none");

            $("#contact-form").find("input[type=text]").val("");
            $("#contact-form").find("textarea").val("");
            $("#contact-form").find("input[type=number]").val("");
            $("#contact-form").find("input[type=email]").val("");
            $("#contact-form").find("#contact-phone").prop("checked", true);
            $("#contact-form").find("#accept-tc").prop("checked", false);
            $("#contact-form").find("select").val("");
            document.querySelector("#contact-form .select-selected").innerHTML =
              "<div>SELECCIONE O ASSUNTO</div>";

            var email_input = document.querySelector(
              "#contact-form #contact-email-active"
            );
            email_input.style.display = "none";

            $("#contact-form .page .text").animate(
              { left: "-100px", opacity: 0 },
              0
            );
            $("#contact-form .p0 .text").animate({ left: 0, opacity: 1 }, 500);

            vm.closeForm();

            setTimeout(() => {
              if (vm.$router.history.current.path != "/") vm.$router.push("/");
            }, 600);
          }
        });
      });
    },
    closeForm() {
      var close_btn = document.querySelector("#contact-form .close-btn");
      var contact_form = document.querySelector("#contact-form");

      $("#contact-form .p1").removeClass("active-pg");
      $("#contact-form .p2").removeClass("active-pg");
      $("#contact-form .p3").removeClass("active-pg");
      $("#contact-form .p0").addClass("active-pg");

      $("#contact-form .page .text").animate({ top: "70px", opacity: 0 }, 0);

      $("#contact-form").find("input[type=text]").val("");
      $("#contact-form").find("textarea").val("");
      $("#contact-form").find("input[type=number]").val("");
      $("#contact-form").find("input[type=email]").val("");
      $("#contact-form").find("#contact-phone").prop("checked", true);
      $("#contact-form").find("#accept-tc").prop("checked", false);
      $("#contact-form").find("select").val("");
      document.querySelector("#contact-form .select-selected").innerHTML =
        "<div>SELECCIONE O ASSUNTO</div>";

      var email_input = document.querySelector(
        "#contact-form #contact-email-active"
      );
      email_input.style.display = "none";

      // contact_form.style.opacity = "0";
      contact_form.style.transform = "translate(60%, -50%)";
      contact_form.style.pointerEvents = "none";
      $("#contact-form .page").css("pointer-events", "none");
      var html_elem = document.documentElement;
      html_elem.style.overflowY = "inherit";
      $("body").css("overflow-y", "inherit");

      setTimeout(() => {
        contact_form.style.transition = "0s";
        contact_form.style.transform = "translate(-160%, -50%)";
      }, 1000);
    },
  },
  mounted() {
    var vm = this;
    vm.addSelectElements(vm);
    document.addEventListener("click", vm.closeAllSelect);
    vm.getFormData();
  },
};
</script>

<style lang="scss">
@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

.balloon {
  width: pxToEm(200);
  height: pxToEm(55);
  color: #fff;
  background-size: contain;
  background-repeat: no-repeat;
  width: pxToEm(190);
  height: pxToEm(50);
  display: none;
  align-items: center;
  justify-content: center;
  align-self: flex-end;
  position: absolute;
  top: 0;
  right: 0;

  @media screen and (max-width: 380px) {
    top: 0px !important;
    left: 50% !important;
    transform: translateX(-50%) !important;
    background-image: none !important;
    background-color: #0babc5 !important;
    border-radius: 10px !important;
    width: 100% !important;
    right: 0 !important;
    height: 30px !important;
    position: relative;
    margin-top: pxToEm(20);
  }

  div {
    color: #fff;
    font-size: pxToEm(16);
    width: 85%;

    @media screen and (max-width: 380px) {
      font-size: 1.375rem !important;
      width: 100%;
    }
  }
}

.input-container {
  display: flex;
  position: relative;

  @media screen and (max-width: 380px) {
    margin-top: pxToEm(30);
    width: 100%;
    flex-direction: column;

    label {
      position: absolute;
      margin-left: 20px;
    }

    input {
      margin-bottom: 0 !important;
    }
  }
}

[tabindex] {
  @media screen and (max-width: 1300px) {
    .input-container {
      flex-direction: column;
      width: 100%;

      input {
        margin-top: 3.75rem;
        margin-bottom: 0 !important;
      }

      label {
        position: absolute;
        margin-left: 20px;
      }

      .balloon {
        top: 0px !important;
        left: 50% !important;
        transform: translateX(-50%) !important;
        background-image: none !important;
        background-color: #0babc5 !important;
        border-radius: 10px !important;
        width: 100% !important;
        right: 0 !important;
        height: 30px !important;
        position: relative;
        margin-top: pxToEm(20);

        div {
          color: #fff;
          font-size: 1.375rem !important;
          width: auto !important;
          margin: 0 !important;
        }
      }
    }
  }
}

#contact-form {
  // opacity: 0;
  pointer-events: none;
  width: 98%;
  height: 98%;
  background-color: rgba(255, 255, 255, 0.95);
  transition: 0.5s;
  z-index: 9999;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-160%, -50%);

  .content-wrapper {
    width: 100%;
    height: 100%;

    .close-btn {
      border-radius: 50%;
      width: pxToEm(54);
      height: pxToEm(54);
      background-color: #0babc5;
      position: absolute;
      right: pxToEm(30);
      top: pxToEm(30);
      cursor: pointer;

      .icon {
        height: 100%;
        width: 100%;

        > .line {
          height: pxToEm(2);
          width: pxToEm(20);
          background-color: #fff;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translateX(-50%) translateY(-50%);

          &:first-child {
            transform: translateX(-50%) translateY(-50%) rotate(45deg);
          }

          &:last-child {
            transform: translateX(-50%) translateY(-50%) rotate(-45deg);
          }
        }
      }
    }

    .pages {
      display: flex;
      justify-content: center;
      height: 100%;
      width: 100%;
      overflow: hidden;

      .page {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        height: auto;
        margin-top: 15%;
        position: absolute;
        opacity: 0;
        transition: 0.5s;
        pointer-events: none;
        width: 100%;
        padding-bottom: 30%;

        @media only screen and (max-width: 767px) {
          top: 50%;
          transform: translateY(-50%);
        }

        &:focus {
          outline: none;
        }

        &.active-pg {
          opacity: 1;
          pointer-events: all;
        }

        .indicators {
          width: pxToEm(720);
          height: pxToEm(105);
          position: relative;
          display: flex;
          justify-content: space-between;

          @media only screen and (max-width: 767px) {
            width: 75%;
          }

          > .line {
            width: 85%;
            height: pxToEm(5);
            background-color: #f6f6f6;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateX(-50%) translateY(-50%);
            z-index: -1;
          }

          .indicator {
            height: pxToEm(105);
            width: pxToEm(105);
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;

            &.active-pg {
              background-color: #f6f6f6;
            }

            .point {
              width: pxToEm(14);
              height: pxToEm(14);
              background-color: #0babc5;
              border-radius: 50%;
            }
          }
        }

        .text {
          margin-top: pxToEm(100);
          display: flex;
          flex-direction: column;
          left: -100px;
          position: relative;
          opacity: 0;

          @media only screen and (max-width: 767px) {
            width: 90%;
            text-align: center;
          }

          .title-success {
            font-size: pxToEm(40);
            color: #707070;
          }

          .copy {
            font-size: pxToEm(20);
            color: #707070;
            // max-width: pxToEm(700);
            width: 100%;
            text-align: center;
            margin-top: pxToEm(30);
          }

          .btn-inicio {
            width: pxToEm(185);
            height: pxToEm(50);
            border-radius: pxToEm(10);
            background-color: #0babc5;
            margin-top: pxToEm(30);
            align-self: flex-end;
            cursor: pointer;

            @media only screen and (max-width: 767px) {
              align-self: center;
            }

            &:focus {
              outline: none;
            }

            .btn {
              display: flex;
              align-items: center;
              justify-content: center;
              height: 100%;

              .txt {
                color: #fff;
                text-transform: uppercase;
              }
            }
          }

          .question {
            font-size: pxToEm(25);
          }

          .radio {
            display: flex;
            margin-top: pxToEm(25);

            .option {
              margin-right: pxToEm(20);
              position: relative;
              height: pxToEm(35);

              .opt {
                color: #0babc5;
                font-size: pxToEm(22);
                display: flex;
                align-items: center;
                justify-content: flex-end;
                width: pxToEm(80);
                height: 100%;
                position: relative;
                padding-right: pxToEm(35);
                font-size: pxToEm(22) x;
                -webkit-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;

                > input {
                  position: absolute;
                  opacity: 0;
                  cursor: pointer;
                  display: none;

                  -webkit-user-select: none;
                  -moz-user-select: none;
                  -ms-user-select: none;
                  user-select: none;

                  &:checked ~ .radio-btn {
                    background-color: #fff;

                    &:after {
                      display: block;
                    }
                  }
                }

                .radio-btn {
                  position: absolute;
                  top: 0;
                  right: pxToEm(-10);
                  height: pxToEm(30);
                  width: pxToEm(30);
                  background-color: #fff;
                  border-radius: 50%;
                  border: pxToEm(3) solid #8ed8e4;
                  cursor: pointer;
                  transform: scale(0.8);

                  &:hover {
                    background-color: #8ed8e4;
                  }

                  &:after {
                    content: "";
                    position: absolute;
                    display: none;
                    top: pxToEm(6);
                    left: pxToEm(6);
                    width: pxToEm(18);
                    height: pxToEm(18);
                    border-radius: 50%;
                    background: #0babc5;
                  }
                }
              }
            }
          }

          .inputs {
            width: pxToEm(990);
            height: auto;
            margin-top: pxToEm(50);
            display: flex;
            flex-direction: column;

            @media only screen and (max-width: 767px) {
              width: 100%;
            }

            input {
              width: 100%;
              height: pxToEm(70);
              border: none;
              border-bottom: pxToEm(2) solid #0babc5;
              font-size: pxToEm(23);
              background-color: transparent;
              margin-bottom: pxToEm(60);

              &::placeholder {
                color: #0babc5;
                opacity: 1;
                text-transform: uppercase;
              }

              &:focus {
                outline: none;
                // border-bottom: pxToEm(2) solid rgba(255, 0, 0, 0.5);

                &::placeholder {
                  // color: rgba(255, 0, 0, 0.5);
                }
              }
            }

            .balloon-email {
              display: flex;

              > div {
                width: 100%;
                margin-left: pxToEm(15);
                margin-top: pxToEm(0);
              }
            }

            .custom-select {
              width: 100%;
              position: relative;

              select {
                display: none;
              }

              .select-selected {
                background-color: DodgerBlue;
                width: 100%;
                height: pxToEm(50);
                border-radius: pxToEm(10);
                border: pxToEm(2) solid #0babc5;
                font-size: pxToEm(23);
                color: #0babc5;
                background: url(../../assets/arrow-select.svg) no-repeat right
                  #fff;
                -webkit-appearance: none;
                background-position-x: 98.5%;
                cursor: pointer;
                text-transform: uppercase;
                display: flex;
                align-items: center;

                > div {
                  margin-left: pxToEm(10);
                }

                &:after {
                  position: absolute;
                  content: "";
                  top: pxToEm(14);
                  right: pxToEm(10);
                  width: 0;
                  height: 0;
                  border: pxToEm(6) solid transparent;
                  border-color: #fff transparent transparent transparent;
                }

                &.select-arrow-active {
                  &:after {
                    border-color: transparent transparent #fff transparent;
                    top: pxToEm(7);
                  }
                }
              }

              .select-items {
                position: absolute;
                top: 100%;
                left: 0;
                right: 0;
                z-index: 99;
                width: 100%;
                height: auto;
                border-radius: pxToEm(10);
                border: pxToEm(2) solid #0babc5;
                background-color: #fff;
                font-size: pxToEm(23);
                color: #707070;
                font-weight: bold;
                -webkit-appearance: none;
                background-position-x: 98.5%;
                cursor: pointer;
                margin-top: pxToEm(15);

                div {
                  height: pxToEm(26);
                  padding: pxToEm(10);

                  &:first-child {
                    border-radius: pxToEm(10) pxToEm(10) 0 0;
                  }

                  &:nth-child(odd) {
                    background-color: #fff;
                  }

                  &:nth-child(even) {
                    background-color: rgba(147, 217, 229, 0.12);
                  }

                  &:last-child {
                    border-radius: 0 0 pxToEm(10) pxToEm(10);
                  }

                  &:hover {
                    background-color: rgba(0, 0, 0, 0.1);
                  }
                }
              }

              .same-as-selected {
                background-color: rgba(0, 0, 0, 0.1);
              }

              .select-hide {
                display: none;
              }
            }

            .accept-tc {
              display: flex;
              flex-direction: row-reverse;
              margin-top: pxToEm(30);

              label {
                a {
                  color: #0babc5;
                }
              }

              input {
                width: pxToEm(20);
                height: pxToEm(20);
                margin: 0;
                margin-right: pxToEm(10);

                &:focus {
                  // outline: 1px solid red;
                }
              }
            }

            button {
              width: pxToEm(185);
              height: pxToEm(50);
              background-color: #0babc5;
              border-radius: pxToEm(10);
              border: none;
              color: #fff;
              font-size: pxToEm(16);
              text-transform: uppercase;
              align-self: flex-end;
              margin-top: pxToEm(40);
              display: flex;
              align-items: center;
              justify-content: center;
              cursor: pointer;

              &:focus {
                outline: none;
              }
            }
          }
        }
      }
    }
  }
}
</style>



