        <div class="line"></div>
      </div>

      <div class="menu-items">
        <div class="menu-item">
          <div class="txt">
            Clínica
            <svg
              class="arrow"
              xmlns="http://www.w3.org/2000/svg"
              xmlns:xlink="http://www.w3.org/1999/xlink"
              viewBox="0 0 4.743 7.308"
            >
              <defs>
                <clipPath id="a">
                  <rect width="4.743" height="7.308" fill="none" />
