              <g clip-path="url(#a)">
                <path
                  d="M3.654,4.743,0,1.088,1.088,0,3.654,2.566,6.22,0,7.308,1.088Z"
                  transform="translate(0 7.308) rotate(-90)"
                  fill="#707070"
                />
              </g>
            </svg>
          </div>
          <div class="submenu">
            <div class="submenu-item">
              <router-link to="/sobre-nos">
