        <div class="menu-item">
          <router-link to="/academia">
            <div class="txt">Academia</div>
          </router-link>
        </div>
        <div class="menu-item">
          <router-link to="/contactos">
            <div class="txt">Contacto</div>
          </router-link>
        </div>
        <div class="menu-item" @click="openContactForm()">
          <div class="btn">
            <div class="txt">Marque a sua consulta</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
// import Logo from "../../assets/acp-logo.svg";
import Logo from "../../assets/logo.png";
import Balloon from "../../assets/menu-balloon.png";

export default {
  data() {
    return {
      logo: Logo,
      balloon: Balloon,
    };
  },
  methods: {
    openSubmenu() {
      $(".menu-wrapper .menu-item").mouseenter(function () {
        if ($(this).find(".submenu").hasClass("active")) {
          $(this).find(".submenu").removeClass("active");
        } else {
          $(".menu-wrapper .menu-item .submenu").removeClass("active");
          $(this).find(".submenu").addClass("active");
        }
      });

      $(".menu-wrapper .menu-item").mouseleave(function () {
        if ($(this).find(".submenu").hasClass("active")) {
          $(this).find(".submenu").removeClass("active");
        } else {
          $(".menu-wrapper .menu-item .submenu").removeClass("active");
          $(this).find(".submenu").addClass("active");
        }
      });
    },
    openMenuMobile() {
      var openBtn = $(".btn-menu");
      var closeBtn = $(".close-menu");
      var menuItems = $(".menu-wrapper-mobile .menu-items");
      var menuItem = $(".menu-wrapper-mobile .menu-item");
      var btn = $(".menu-wrapper-mobile .menu-item .btn");

      openBtn.toggleClass("open", 500);
      closeBtn.toggleClass("open", 500);
      menuItems.toggleClass("open", 500);
      menuItem.toggleClass("open", 500);
      btn.toggleClass("open", 500);
      menuItems.css("height", $(window).height());
      $("body, html").toggleClass("open");
    },
    openSubmenuMobile() {
      $(".menu-wrapper-mobile .menu-item").click(function () {
        if ($(this).find(".submenu").hasClass("active")) {
          $(this).find(".submenu").removeClass("active");
        } else {
          $(".menu-wrapper-mobile .menu-item .submenu").removeClass("active");
          $(this).find(".submenu").addClass("active");
        }
      });
    },
    openContactForm() {
      var contact_form = document.querySelector("#contact-form");
      var menu = document.querySelector("#menu");

      contact_form.style.transition = "1s";
      contact_form.style.transform = "translate(-50%, -50%)";
      contact_form.style.pointerEvents = "all";
      $("#contact-form .page").css("pointer-events", "all");
      var html_elem = document.documentElement;
      html_elem.style.overflow = "hidden";

      $(".menu-wrapper-mobile .menu-items").removeClass("open");
      $(".menu-wrapper-mobile .btn-menu").removeClass("open");
      $(".menu-wrapper-mobile .close-menu").removeClass("open");
      $(".menu-wrapper-mobile .menu-item").removeClass("open");

      $("#contact-form .p0 .text").animate({ left: 0, opacity: 1 }, 500);
    },
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    this.openSubmenu();
    this.openSubmenuMobile();
    this.FadeIn();
  },
  watch: {
    $route(to, from) {
      $(".menu-wrapper-mobile .menu-items").removeClass("open");
      $(".menu-wrapper-mobile .btn-menu").removeClass("open");
      $(".menu-wrapper-mobile .close-menu").removeClass("open");
      $(".menu-wrapper-mobile .menu-items").removeClass("open");
      $(".menu-wrapper-mobile .menu-items .menu-item").removeClass("open");
      $(".menu-wrapper-mobile .menu-items .menu-item .btn").removeClass("open");
      $("body, html").removeClass("open");

      document.body.scrollTop = 0; // For Safari
      document.documentElement.scrollTop = 0;
      $("#main-section").css("margin-top", $("#menu").height());
      this.FadeIn();
    },
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: rgba(11, 171, 197, 0.6);

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(65);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

#menu {
  background-color: $lt-gray;
  width: 100%;
  position: fixed;
  z-index: 999;
  box-shadow: 2px -2px 17px rgba(0, 0, 0, 0.4);
  padding: 0;
  top: 0;

  .menu-wrapper {
    height: pxToEm(187);
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 pxToEm(130);

    @media only screen and (min-width: 1200px) {
      margin: 0 pxToEm(80);
    }

    @media only screen and (min-width: 992px) and (max-width: 1199px) {
      margin: 0 pxToEm(60);
    }

    @media only screen and (min-width: 768px) and (max-width: 991px) {
      margin: 0 pxToEm(40);
    }

    @media only screen and (max-width: 767px) {
      display: none;
    }

    > a {
      width: auto;
      height: 100%;
      display: flex;

      img {
        width: 85%;
        height: 85%;
        align-self: center;
        justify-self: center;
        will-change: auto;
      }

      .logo {
        background-size: 80%;
        background-position: center;
        background-repeat: no-repeat;
        width: pxToEm(187);
        height: 100%;
      }
    }

    .menu-items {
      max-width: pxToEm(750);
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 100%;
      display: flex;

      .menu-item {
        position: relative;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        width: pxToEm(120);
        cursor: pointer;

        a {
          color: #707070;

          &:hover {
            color: $turquiose;
          }
        }

        &:last-child {
          width: auto;
        }

        .txt {
          display: flex;
          align-items: center;
          color: #707070;
          font-size: pxToEm(24);
          transition: 0.5s;

          &:hover {
            color: $turquiose;
          }

          .arrow {
            width: pxToEm(4.7);
            height: pxToEm(7.23);
            transform: rotate(90deg);
            margin-left: pxToEm(10);
          }
        }

        .submenu {
          width: pxToEm(170);
          height: pxToEm(120);
          position: absolute;
          background-size: contain;
          background-repeat: no-repeat;
          padding: pxToEm(35);
          top: pxToEm(110);
          left: pxToEm(3);
          z-index: 9999;
          display: none;
          flex-direction: column;
          justify-content: flex-start;

          &.active {
            display: flex;
          }

          .submenu-item {
            border-bottom: pxToEm(1) solid #def5f9;
            height: pxToEm(40);
            display: flex;
            align-items: center;
            color: #707070;

            &:last-child {
              border: none;
            }

            a {
              color: #707070;
              transition: 0.5s;

              &:hover {
                color: $turquiose;
              }
            }
          }
        }

        .btn {
          width: pxToEm(214);
          height: pxToEm(40);
          border: pxToEm(2) solid $turquiose;
          display: flex;
          transition: 0.5s;

          &:hover {
            background-color: $turquiose;

            .txt {
              color: $white;
            }
          }

          .txt {
            font-family: "OS-Regular";
            font-weight: bold;
            text-transform: uppercase;
            font-size: pxToEm(14);
          }
        }
      }
    }
  }

  .menu-wrapper-mobile {
    height: pxToEm(100);
    display: none;
    justify-content: space-between;
    align-items: center;
    margin: 0 pxToEm(20);
    pointer-events: inherit;

    @media only screen and (max-width: 767px) {
      display: flex;
    }

    > a {
      width: auto;
      height: 100%;
      display: flex;

      img {
        height: 80%;
        align-self: center;
        justify-self: center;
        will-change: auto;
      }

      .logo {
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        width: pxToEm(90);
        height: 95%;
      }
    }

    .btn-menu {
      width: pxToEm(50);
      height: pxToEm(45);
      padding: pxToEm(20);
      margin-right: pxToEm(10);
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: flex-end;
      position: absolute;
      right: 0;
      opacity: 1;
      pointer-events: inherit;

      &.open {
        opacity: 0;
        pointer-events: none;
      }

      .line {
        width: 100%;
        height: pxToEm(3);
        background-color: $turquiose;

        &:nth-child(2) {
          width: 80%;
        }

        &:nth-child(3) {
          width: 60%;
        }
      }
    }

    .close-menu {
      width: pxToEm(50);
      height: pxToEm(50);
      margin-right: pxToEm(20);
      position: absolute;
      opacity: 0;
      pointer-events: none;
      right: 0;
      display: block;
      pointer-events: inherit;

      &.open {
        opacity: 1;
        pointer-events: all;
      }

      .line {
        width: 100%;
        height: pxToEm(3);
        background-color: $turquiose;
        position: absolute;
        top: 50%;

        &:nth-child(1) {
          transform: rotate(45deg);
        }

        &:nth-child(2) {
          transform: rotate(-45deg);
        }
      }
    }

    .menu-items {
      justify-content: flex-start;
      align-items: center;
      height: 85vh;
      position: absolute;
      top: pxToEm(100);
      right: 0;
      width: 100vw;
      flex-direction: column;
      max-width: 100%;
      background-color: #f6f6f6;
      padding: pxToEm(50) 0;
      opacity: 0;
      pointer-events: none;
      display: flex;
      z-index: 9999999;

      &.open {
        opacity: 1;
        pointer-events: all;
      }

      .menu-item {
        position: relative;
        height: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        cursor: pointer;
        flex-direction: column;
        margin-top: pxToEm(30);
        pointer-events: none;

        &.open {
          pointer-events: all;
        }

        a {
          color: #707070;
        }

        &:first-child {
          margin: 0;
        }

        &:last-child {
          width: auto;
        }

        .txt {
          color: #707070;
          font-size: pxToEm(28);

          .arrow {
            width: pxToEm(12.5);
            height: pxToEm(12.5);
            transform: rotate(90deg);
            margin-left: pxToEm(10);
          }
        }

        .submenu {
          width: 100%;
          height: auto;
          display: none;
          flex-direction: column;
          justify-content: space-between;
          margin-top: pxToEm(20);

          &.active {
            display: flex;
          }

          .submenu-item {
            border-bottom: pxToEm(1) solid $turquiose;
            height: pxToEm(80);
            display: flex;
            align-items: center;
            align-items: center;
            margin: 0 30%;
            justify-content: center;

            &:last-child {
              border: none;
            }

            a,
            .text {
              color: #707070;
              font-size: pxToEm(20);
            }
          }
        }

        .btn {
          width: pxToEm(270);
          height: pxToEm(60);
          border: pxToEm(2) solid $turquiose;
          display: flex;
          transition: 0.5s;
          pointer-events: none;

          &.open {
            pointer-events: all;
          }

          &:hover {
            background-color: $turquiose;

            .txt {
              color: $white;
            }
          }

          .txt {
            font-family: "OS-Regular";
            font-weight: bold;
            text-transform: uppercase;
            font-size: pxToEm(18);
          }
        }
      }
    }
  }
}
</style>



