<!-- eslint-disable -->
<template>
  <div id="gdpr-box" class="cookie-bar">
    <p>
      Este site utiliza cookies para lhe fornecer uma experiência de utilizador melhorada. Ao usar este site, está a aceitar a nossa
      <router-link to="/politica-cookies" target="_blank">Política de Cookies</router-link>.
    </p>
    <button class="gdpr-button-accept">Aceitar cookies</button>
  </div>
</template>

<script>
/* eslint-disable */
import Cookies from "js-cookie";

export default {
  mounted() {
    var cookieBar = document.getElementById("gdpr-box");
    var acceptBtn = document.querySelector(".gdpr-button-accept");

    if (Cookies.get("cookie-consent-anacarolinapereira") == undefined) {
      cookieBar.style.display = "flex";

      acceptBtn.addEventListener("click", function () {
        Cookies.set("cookie-consent-anacarolinapereira", "yes", {
          expires: 365,
          path: "/",
        });
        cookieBar.style.display = "none";
      });
    } else {
      cookieBar.style.display = "none";
    }
  },
};
</script>

<style lang="scss" scoped>
@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1em;
}

#gdpr-box {
  display: flex;
  width: 100%;
  background: #0babc5;
  color: #fff;
  position: fixed;
  bottom: 0;
  z-index: 999;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: pxToEm(30) 0;

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    flex-direction: column;
  }

  p {
    padding: pxToEm(10) pxToEm(20);
    font-size: pxToEm(20);
    line-height: pxToEm(18);
    margin: pxToEm(5) 0;
    text-align: center;
  }

  a {
    color: #000;
    cursor: pointer;
  }

  button.gdpr-button-accept {
    background: none;
    color: #fff;
    font-size: pxToEm(14);

    &:hover {
      cursor: pointer;
    }
  }

  button.gdpr-button-accept {
    border: pxToEm(1) solid #fff;
    border-radius: pxToEm(10);
    padding: pxToEm(15) pxToEm(20);
    text-decoration: none;
    margin: 0 pxToEm(20);
    font-weight: 800;
    font-size: pxToEm(16);
    transition: all 0.5s;

    &:hover {
      background: #fff;
      color: #0babc5;
    }
  }
}
</style>


