<template>
  <div id="app">
    <script type="application/ld+json">
  {
    "@context": "https://schema.org",
    "@type": "PublicHealth",
    "name": "Ana Carolina Pereira - Clínica de Psicologia",
    "image": "",
    "@id": "",
    "url": "https://www.anacarolinapereira.pt",
    "telephone": "",
    "address": {
      "@type": "PostalAddress",
      "streetAddress": "Rua Machado de Castro, 7, R/C",
      "addressLocality": "Coimbra",
      "postalCode": "3000-254",
      "addressCountry": "PT"
    },
    "geo": {
      "@type": "GeoCoordinates",
      "latitude": 40.2136424,
      "longitude": -8.4188992
    },
    "sameAs": [
      "https://www.facebook.com/anacarolinapereirapc/",
      "https://www.instagram.com/anacarolinapereirapc/"
    ]
  }
    </script>
    <loading-screen :isLoading="isLoading"></loading-screen>
    <app-header></app-header>
    <router-view v-if="!isLoading" id="main-section" style="flex: 1 1 auto; overflow: hidden;"></router-view>
    <app-footer></app-footer>
    <marcar-consulta-form></marcar-consulta-form>
    <contact-form></contact-form>
    <cookie-bar></cookie-bar>
    <back-to-top></back-to-top>
    <popup></popup>
  </div>
</template>

<script>
/* eslint-disable */
import Header from "./components/includes/Header";
import Footer from "./components/includes/Footer";
import MarcarConsultaForm from "./components/includes/Marcar-consulta-form";
import ContactForm from "./components/includes/Contact-form";
import CookieBar from "./components/includes/CookieBar";
import LoadingScreen from "./components/includes/LoadingScreen";
import BackToTop from "./components/includes/back-to-top";
import Popup from "./components/includes/popup";
import axios from "axios";

export default {
  data() {
    return {
      isLoading: true,
    };
  },
  components: {
    appHeader: Header,
    appFooter: Footer,
    marcarConsultaForm: MarcarConsultaForm,
    contactForm: ContactForm,
    cookieBar: CookieBar,
    loadingScreen: LoadingScreen,
    backToTop: BackToTop,
    popup: Popup,
  },
  methods: {
    offsetTop() {
      var menu = $("#menu");
      $("#main-section").css("margin-top", menu.height());
    },
    stickyFooter() {
      var totalHeight = $("body").height();
      if ($(window).height() > totalHeight) {
        var routerSection = $("#app > #main-section");
        var minSectionHeight = $(window).height() - totalHeight;

        routerSection.css(
          "min-height",
          routerSection.height() + minSectionHeight + "px"
        );
      }
    },
  },
  mounted() {
    var vm = this;

    setTimeout(() => {
      $("body, html").css("overflow-y", "inherit");
      vm.isLoading = false;
    }, 2000);

    vm.offsetTop();
    // vm.stickyFooter();

    window.addEventListener("resize", function () {
      vm.offsetTop();
      vm.stickyFooter();
    });

    window.addEventListener("load", function () {
      vm.offsetTop();
      vm.stickyFooter();
    });

    var contact_form = document.querySelector("#contact-form");
    var contact_form2 = document.querySelector("#contact-form-2");

    contact_form.style.transform = "translate(-160%, -50%)";
    contact_form2.style.transform = "translate(-160%, -50%)";
  },
  updated() {
    var vm = this;
    vm.stickyFooter();
    vm.offsetTop();

    setTimeout(() => {
      $("body, html").css("overflow-y", "inherit");
      vm.isLoading = false;
    }, 2000);
  },
};
</script>

<style lang="scss">
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

//paddings and margins
$pd-desktop: 0 130px;
$mg-desktop: 0 40px;

//circle
$circle: 50%;

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: $turquiose;
  border-radius: 10px;
  transition: 0.5s;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #0b92a7;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1em;
}

.info-wrapper {
  .topic {
    > div {
      span {
        font-size: pxToEm(16) !important;
      }
    }
    li {
      margin: pxToEm(10) 0;

      span {
        font-size: pxToEm(16) !important;
      }
    }
  }
}

p {
  margin: 0;
}

@font-face {
  font-family: "Cabin-Regular";
  src: url("../src/assets/fonts/Cabin-Regular.ttf");
}

@font-face {
  font-family: "OS-Regular";
  src: url("../src/assets/fonts/OpenSans-Regular.ttf");
}

@font-face {
  font-family: "OS-Bold";
  src: url("../src/assets/fonts/OpenSans-Bold.ttf");
}

@font-face {
  font-family: "OS-ExtraBold";
  src: url("../src/assets/fonts/OpenSans-ExtraBold.ttf");
}

@font-face {
  font-family: "OS-Light";
  src: url("../src/assets/fonts/OpenSans-Light.ttf");
}

@font-face {
  font-family: "OS-SemiBold";
  src: url("../src/assets/fonts/OpenSans-SemiBold.ttf");
}

@font-face {
  font-family: "Dalmatins";
  src: url("../src/assets/fonts/Dalmatins.otf");
}

html,
body {
  font-family: Cabin-Regular;
  margin: 0;
  padding: 0;
  height: auto;
  user-select: none;
  font-size: 11px;
  overflow: hidden;

  &.open {
    overflow-y: hidden;
  }

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    font-size: 9.4px;
  }

  @media only screen and (min-width: 768px) and (max-width: 1199px) {
    font-size: 11px;
  }

  @media only screen and (max-width: 767px) {
    font-size: 10px;
  }

  a {
    text-decoration: none;
    color: #0babc5;
  }
}

#app {
  background-color: #fff;
  height: auto;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.btn {
  border-radius: pxToEm(10);
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: all;

  &.blue {
    border: pxToEm(2) solid $turquiose;
    background-color: $turquiose;
    transition: 0.5s;
    cursor: pointer;

    .txt {
      color: $white;
    }

    &:hover {
      border: pxToEm(2) solid $turquiose;
      background-color: $white;
      .txt {
        color: $turquiose;
      }
    }
  }

  &.white {
    border: pxToEm(2) solid #cbcbcb;
    background-color: $white;
    transition: 0.5s;
    cursor: pointer;

    .txt {
      color: #cbcbcb;
    }

    &:hover {
      border: pxToEm(2) solid #cbcbcb;
      background-color: #cbcbcb;
      .txt {
        color: $white;
      }
    }
  }

  &.white-blue {
    border: pxToEm(2) solid #cbcbcb;
    background-color: $white;
    transition: 0.5s;
    cursor: pointer;

    .txt {
      color: #0babc5;
    }

    &:hover {
      border: pxToEm(2) solid #0babc5;
      background-color: #0babc5;
      .txt {
        color: $white;
      }
    }
  }
}

.title {
  &.main {
    font-size: pxToEm(42);
  }
}

.movement {
  position: relative;
  top: 70px;
  opacity: 0;
  transition: top 1s cubic-bezier(0.15, 0.37, 0.7, 1) 0s,
    opacity 0.4s linear 0.1s;

  &.placed {
    top: 0;
    opacity: 1;
  }

  &:nth-child(2) {
    transition: top 1s cubic-bezier(0.15, 0.37, 0.7, 1) 0.2s,
      opacity 0.5s linear 0.2s;
  }

  &:nth-child(3) {
    transition: top 1s cubic-bezier(0.15, 0.37, 0.7, 1) 0.3s,
      opacity 0.6s linear 0.3s;
  }

  &:nth-child(4) {
    transition: top 1s cubic-bezier(0.15, 0.37, 0.7, 1) 0.4s,
      opacity 0.7s linear 0.4s;
  }
}

.movement-delayed {
  position: relative;
  top: 70px;
  opacity: 0;
  transition: top 1s cubic-bezier(0.15, 0.37, 0.7, 1) 0s,
    opacity 0.4s linear 0.1s;

  &.placed {
    top: 0;
    opacity: 1;
  }
}

.lateral {
  position: relative;
  left: -100px;
  opacity: 0;
  transition: left 1s cubic-bezier(0.15, 0.37, 0.7, 1) 0s,
    opacity 0.4s linear 0.1s;

  &.placed {
    left: 0;
    opacity: 1;
  }
}

.lateralR {
  position: relative;
  right: -100px;
  opacity: 0;
  transition: right 1s cubic-bezier(0.15, 0.37, 0.7, 1) 0s,
    opacity 0.4s linear 0.1s;

  &.placed {
    right: 0;
    opacity: 1;
  }
}
</style>



