/* eslint-disable */
import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

var routes = []

function lazyLoad(view) {
  return () => import(`@/components/${view}.vue`)
}

function FadeIn() {
  var allMods = $(".movement, .cover");
  var delayedMods = $(".movement-delayed");
  var lateralMods = $(".lateral, .lateralR");
  var svg = $("#svg");

  (function ($) {
    $.fn.visible = function (partial, adjust) {
      if (adjust === undefined) {
        adjust = 0;
      }
      var elem = $(this),
        wind = $(window),
        viewTop = wind.scrollTop(),
        viewBottom = viewTop + wind.height(),
        elem_top = elem.offset().top + adjust,
        elem_bottom = elem_top + elem.height() - adjust,
        compareTop = partial === true ? elem_bottom : elem_top,
        compareBottom = partial === true ? elem_top : elem_bottom;

      return compareBottom <= viewBottom && compareTop >= viewTop;
    };
  })(jQuery);

  var win = $(window);

  var xmlTeste = true;

  allMods.each(function (i, el) {
    var el = $(el);
    if (el.visible(true)) {
      el.addClass("placed");
    }
  });
  svg.each(function (i, el) {
    var el = $(el);
    if (el.visible(true)) {
      xmlTeste &&
        el.append(
          "<img src='assets/img/elemento_animated.svg' alt=''></img>"
        );
      xmlTeste = false;
    }
  });
  lateralMods.each(function (i, el) {
    var el = $(el);
    if (el.visible(true, 100)) {
      el.addClass("placed");
    }
  });

  delayedMods.each(function (i, el) {
    var el = $(el);
    if (el.visible(true, 800)) {
      el.addClass("placed");
    }
  });

  win.scroll(function (event) {
    allMods.each(function (i, el) {
      var el = $(el);
      if (el.visible(true)) {
        el.addClass("placed");
      }
    });
    lateralMods.each(function (i, el) {
      var el = $(el);
      if (el.visible(true, 100)) {
        el.addClass("placed");
      }
    });
    delayedMods.each(function (i, el) {
      var el = $(el);
      if (el.visible(true, 800)) {
        el.addClass("placed");
      }
    });
    svg.each(function (i, el) {
      var el = $(el);
      if (el.visible(true)) {
        xmlTeste &&
          el.append(
            "<img src='assets/img/elemento_animated.svg' alt=''></img>"
          );
        xmlTeste = false;
      }
    });
  });
}

routes = [
  {
    path: '*',
    //component: NotFoundComponent
  },
  {
    path: '/',
    name: 'index',
    component: lazyLoad('index')
  },
  {
    path: '/sobre-nos',
    name: 'sobre-nos',
    component: lazyLoad('sobre-nos')
  },
  {
    path: '/servicos',
    name: 'servicos',
    component: lazyLoad('servicos')
  },
  {
    path: '/servicos/:name',
    name: 'servico',
    component: lazyLoad('servicos/servico/servico-page')
  },
  {
    path: '/academia',
    name: 'academia',
    component: lazyLoad('academia')
  },
  {
    path: '/academia/formacao/:name',
    name: 'formacao',
    component: lazyLoad('academia/formacao/formacao-page')
  },
  {
    path: '/academia/curso/:name',
    name: 'curso',
    component: lazyLoad('academia/curso/curso-page')
  },
  {
    path: '/academia/webinar/:name',
    name: 'webinar',
    component: lazyLoad('academia/webinar/webinar-page')
  },
  {
    path: '/perguntas-frequentes',
    name: 'perguntas-frequentes',
    component: lazyLoad('perguntas-frequentes')
  },
  {
    path: '/contactos',
    name: 'contactos',
    component: lazyLoad('contactos')
  },
  {
    path: '/politica-privacidade',
    name: 'politica-privacidade',
    component: lazyLoad('politica-privacidade')
  }
]

const router = new VueRouter({
  mode: 'history',
  routes,
  // scrollBehavior: function (to) {
  //   if (to.hash) {
  //     console.log($(to.hash).offset());
  //     return $('html,body').stop().animate({ scrollTop: $(to.hash).offset().top - $(to.hash).height() }, 1000)
  //   }
  // }
})

router.afterEach((to, from, next) => {
  $("body, html").removeClass("open");
  $('.menu-wrapper-mobile .btn-menu').removeClass('open');
  $('.menu-wrapper-mobile .close-menu').removeClass('open');
  $(".menu-wrapper-mobile .menu-items").removeClass("open");
  $(".menu-wrapper-mobile .menu-items .menu-item").removeClass("open");
  $(".menu-wrapper-mobile .menu-items .menu-item .btn").removeClass("open");

  document.body.scrollTop = 0; // For Safari
  document.documentElement.scrollTop = 0
  $('#main-section').css('margin-top', $('#menu').height());

  // FadeIn();

  $('#popup').removeClass('active');

  if (to.name != 'servicos') {
    $('.home').css('display', 'none');
    $('.no-home').css('display', 'flex');
  } else {
    $('.home').css('display', 'flex');
    $('.no-home').css('display', 'none');
  }

  if (to.name != 'sobre-nos') {
    $('.sb').css('display', 'none');
    $('.no-sb').css('display', 'flex');
  } else {
    $('.sb').css('display', 'flex');
    $('.no-sb').css('display', 'none');
  }
})

export default router;



