<template>
  <div id="loading" :class="{ fadeout: !isLoading }">
    <div class="loader"></div>
  </div>
</template>

<script>
/* eslint-disable */
export default { name: "LoadingScreen", props: ["isLoading"] };
</script>

<style lang="scss" scoped>
#loading {
  background-color: #fff;
  font-size: 32px;
  height: 100vh;
  width: 100vw;
  z-index: 99999999999999999999;
  position: absolute;
  opacity: 1;
  visibility: visible;
  transition: 1.5s;

  &.fadeout {
    // animation: fadeout 2s forwards;
    opacity: 0;
    visibility: hidden;
  }
}

.loader {
  position: absolute;
  border: 6px solid #cbcbcb;
  border-top: 6px solid #3498db;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  left: calc(50% - 15px);
  top: calc(50% - 15px);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes fadeout {
  to {
    opacity: 0;
    visibility: hidden;
  }
}
</style>


