import Vue from 'vue'

import App from './App.vue'
import router from './routes'

import VueMeta from 'vue-meta'
// import VueMoment from 'vue-moment'

Vue.use(VueMeta, {
  // optional pluginOptions
  refreshOnceOnNavigation: true
})

// Vue.use(VueMoment)

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



