<!-- eslint-disable -->
<template>
  <div id="popup">
    <div class="close-btn" @click="close()">
      <div class="icon">
        <div class="line"></div>
        <div class="line"></div>
      </div>
    </div>
    <div class="card" :class="{'is-visible': is_visible == 1}">
      <div class="card-img" :style="{'background-image': 'url(' + img + ')'}"></div>
      <div class="card-content">
        <div class="card-header">
          <div class="card-type">{{categoria}}</div>
          <div class="card-status" :class="{'is-full': is_full == 1, 'has-vagas': last_vagas == 1}">
            <span :class="{'is-full': is_full == 1}">vagas preenchidas</span>
            <span :class="{'has-vagas': last_vagas == 1}">últimas vagas</span>
          </div>
        </div>
        <div class="card-body">
          <h3 class="card-title">{{title}}</h3>
          <div class="card-date">
            <div class="txt">
              {{start}}
              <br />
              {{end}}
            </div>
            <div class="icon" :style="{'background-image': 'url(' + calendar + ')'}"></div>
          </div>
        </div>
      </div>
      <router-link :to="url">
        <div class="card-cta">
          <div class="txt">Saiba mais</div>
        </div>
      </router-link>
    </div>
    <!-- <div class="content">
      <div class="img" :style="{'background-image': 'url(' + img + ')'}"></div>
      <div class="cont">
        <div class="subtitle">Formação em destaque</div>
        <div class="title">{{title}}</div>
        <div class="date">De dia {{date_start}} a {{date_end}}</div>
        <router-link :to="url" target="_blank">
          <div class="cta">
            <div class="btn">
              <div class="txt">Saber mais</div>
            </div>
          </div>
        </router-link>
      </div>
    </div>-->
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import calendar from "../../assets/calendar.svg";
import moment from "moment";

export default {
  data() {
    return {
      calendar,
      id: "",
      title: "",
      text: "",
      url: "",
      img: "",
      start: "",
      end: "",
      is_full: "",
      last_vagas: "",
      is_visible: "",
      categoria: "",
    };
  },
  methods: {
    close() {
      $("#popup").removeClass("active");
    },
  },
  mounted() {
    var vm = this;
    var map = Array.prototype.map;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getFormacaoInfo.php")
      .then((resp) => {
        const data = resp.data;
        var info = [];

        map.call(data, function (d) {
          if (d.in_popup == 1) {
            var src = d.icon.split("uploads");
            var ds;
            var de;
            if (d.date_start == "0000-00-00") {
              ds = "disponível brevemente";
              de = "";
            } else {
              ds = moment(d.date_start).format("DD-MM-YYYY");
              de = moment(d.date_end).format("DD-MM-YYYY");
            }

            var categoria;
            if (d.categoria_id == 1) categoria = "formacao";
            else if (d.categoria_id == 2) categoria = "curso";
            else categoria = "webinar";

            var categoria_title;
            if (d.categoria_id == 1) categoria_title = "formação";
            else if (d.categoria_id == 2) categoria_title = "curso";
            else categoria_title = "webinar";

            vm.$data.title = d.title;
            vm.$data.start = ds;
            vm.$data.end = de;
            vm.$data.url = "/academia/" + categoria + d.link;
            vm.$data.img =
              "https://beta.anacarolinapereira.pt/uploads" + src[1];
            vm.$data.is_full = d.is_full;
            vm.$data.last_vagas = d.last_vagas;
            vm.$data.is_visible = d.is_visible;
            vm.$data.categoria = categoria_title;
          }
        });
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(130);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

#popup {
  width: auto;
  height: auto;
  position: fixed;
  z-index: 99;
  bottom: pxToEm(40);
  left: -150%;
  transition: 1s;

  &.active {
    left: pxToEm(20);
  }

  .close-btn {
    border-radius: 50%;
    width: pxToEm(34);
    height: pxToEm(34);
    background-color: #0babc5;
    position: absolute;
    right: pxToEm(15);
    top: pxToEm(15);
    cursor: pointer;

    .icon {
      height: 100%;
      width: 100%;

      > .line {
        height: pxToEm(2);
        width: pxToEm(15);
        background-color: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);

        &:first-child {
          transform: translateX(-50%) translateY(-50%) rotate(45deg);
        }

        &:last-child {
          transform: translateX(-50%) translateY(-50%) rotate(-45deg);
        }
      }
    }
  }

  .card {
    width: pxToEm(500);
    height: pxToEm(530);
    background-color: $white;
    border-radius: pxToEm(43);
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    transition: 0.5s;
    // margin: pxToEm(50) pxToEm(10) pxToEm(50) pxToEm(10);
    box-shadow: pxToEm(3) pxToEm(3) pxToEm(15) rgba(0, 0, 0, 0.2);

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      width: 90%;
    }

    &.is-visible {
      display: flex;
    }

    .card-img {
      background-color: #cbcbcb;
      border-radius: pxToEm(43);
      width: 100%;
      height: 70%;
      background-size: cover;
      background-position: center;
    }

    .card-content {
      display: flex;
      flex-direction: column;
      padding: pxToEm(30) pxToEm(30);
      height: 30%;

      @media only screen and (min-width: 300px) and (max-width: 599px) {
        width: 90%;
      }

      .card-header {
        display: flex;
        justify-content: space-between;

        .card-type {
          text-transform: uppercase;
          color: $turquiose;
          font-family: "Arial";
          font-size: pxToEm(20);
          font-weight: 600;
        }

        .card-status {
          width: pxToEm(200);
          border: pxToEm(1) solid $turquiose;
          border-radius: pxToEm(20);
          text-transform: uppercase;
          font-family: "Arial";
          align-items: center;
          justify-content: center;
          display: none;

          &.is-full {
            background-color: $white;
            color: $turquiose;
            display: flex;

            span {
              display: none;

              &.is-full {
                display: block;
              }
            }
          }

          &.has-vagas {
            background-color: $turquiose;
            color: $white;
            display: flex;

            span {
              display: none;

              &.has-vagas {
                display: block;
              }
            }
          }
        }
      }

      .card-body {
        display: flex;
        justify-content: space-between;
        margin-top: pxToEm(15);

        .card-title {
          margin: 0;
          font-family: "Arial";
          color: #707070;
          font-weight: bold;
          font-size: pxToEm(22);
          max-width: 65%;
          width: 100%;
          height: pxToEm(76);
        }

        .card-date {
          display: flex;
          justify-content: space-between;
          width: 40%;
          text-align: right;

          .txt {
            width: 100%;
            margin-right: pxToEm(10);
          }

          .icon {
            width: 3.6rem;
            height: 2.8rem;
            background-size: cover;
            background-repeat: no-repeat;
          }
        }
      }
    }

    a {
      position: absolute;
      width: pxToEm(220);
      height: pxToEm(55);
      bottom: pxToEm(-27.5);

      .card-cta {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $turquiose;
        border: pxToEm(1) solid $turquiose;
        border-radius: pxToEm(20);
        transition: 0.5s;
        cursor: pointer;

        .txt {
          color: #fff;
          font-size: pxToEm(22);
        }

        &:hover {
          background-color: $white;

          .txt {
            color: $turquiose;
          }
        }
      }
    }
  }

  // .content {
  //   display: flex;
  //   flex-direction: column;
  //   align-items: center;
  //   justify-content: center;

  //   .img {
  //     width: 100%;
  //     height: pxToEm(300);
  //     border-radius: pxToEm(20);
  //     background-color: #cbcbcb;
  //   }

  //   .cont {
  //     padding: pxToEm(25);
  //     display: flex;
  //     flex-direction: column;
  //     align-items: center;

  //     > div {
  //       margin-bottom: pxToEm(10);
  //     }

  //     .title {
  //       font-size: pxToEm(24);
  //       color: #0babc5;
  //     }

  //     .cta {
  //       width: pxToEm(185);
  //       height: pxToEm(50);
  //       border-radius: pxToEm(10);
  //       border: pxToEm(1) solid #0babc5;
  //       background-color: #0babc5;
  //       // margin: pxToEm(15) 0;
  //       margin-top: pxToEm(15);
  //       cursor: pointer;
  //       transition: 0.5s;

  //       &:hover {
  //         background-color: #fff;

  //         .btn {
  //           .txt {
  //             color: #0babc5;
  //           }
  //         }
  //       }

  //       &:focus {
  //         outline: none;
  //       }

  //       .btn {
  //         display: flex;
  //         align-items: center;
  //         justify-content: center;
  //         height: 100%;

  //         .txt {
  //           color: #fff;
  //           text-transform: uppercase;
  //         }
  //       }
  //     }
  //   }
  // }
}
</style>



